@font-face {
  font-family: FiraCode;
  src: url("/fonts/FiraCode-Regular.ttf");
  font-weight: normal;
  font-style: normal;
}

/* Media Queries */
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
}

body {
  color: #212121;
  font-family: "Open Sans", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #212121;
  font-family: "Abel", sans-serif;
}

code {
  background-color: #000;
  border: 1px solid #262626;
  border-radius: 4px;
  color: #fff;
  font-family: "FiraCode";
  font-weight: lighter;
  padding: 10px 20px;
  margin: 10px;
}

code::before {
  color: #00cc00;
  content: '$ ';
}

.site-container {
  margin: auto;
  max-width: 1170px;
  width: 100%;
}
